import tabBar from '../../common/tabbar.vue';
import http from '../../common/http.js';
export default {
	data() {
		return {
			platForm: 0,
			iconSize: 0,
			keyword: '',
			isIphoneX: false,
			navList: [],
			contentHeight: 543,
		}
	},
	mounted() {
		
	},
	components:{
		tabBar
	},
	methods: {
		getNavList() {
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			let _self = this;
			const url = apiPrefix + "?mod=loveword&ac=get_all_cid";
			http.request(url, {
				auth: auth
			}).then( resp => {
				//console.log('resp pages/index/index:', resp);
				const n = resp.length;
				for(let j=0;j<n;j++) {
					let secondNavList = resp[j].secondNav;
					let secondNavLen  = secondNavList.length;
					for(let m=0;m<secondNavLen;m++) {
						// navClass
						if((m+1)%3 == 0) {
							if(m>2) {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-top'; 
							} else {
								resp[j].secondNav[m].navClass = ' second-nav-label '; 
							}
						} else {
							if(m>2) {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-margin second-nav-lable-top'; 
							} else {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-margin '; 
							}
						}
					}
				}
				_self.navList = resp;		
				//_self.contentHeight = _self.calculateNavHeight(resp);
			});
		},
		calculateNavHeight(navList) {
			// 判断nav 高度
			let n = navList.length;
			let sumHeight = 0;
			for (let j = 0;j<n;j++)
			{
				let oneNavHeight = 25+20+13;
				let secondNav = navList[j].secondNav;
				let secondLen = secondNav.length;
				if (secondLen>3) {
					let lineNum = Math.ceil(secondLen/3);
					oneNavHeight+=30*lineNum + (lineNum-1)*12;
				} else {
					oneNavHeight+=30;
				}
				//底部bottom
				oneNavHeight+=20;
				sumHeight+=oneNavHeight;
			}
			sumHeight+=60;
			return sumHeight;
		},
		// 跳转到关键词页面
		searchKeyword() {
			if(!this.keyword || this.keyword.length<2) {
				uni.showToast({
					title: '请输入关键词，且至少2个字符长度!',
					icon:"none",
					duration: 2000
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/index/huashu?keyword='+this.keyword
			});
		},
		handleContact() {
			console.log('contact ... handler...');
		},
		search() {
			
		},
		upgrade_vip() {
			if(this.platForm == 2) {
				return;
			}
			// 升级
			let userInfo = this.$store.getters.userInfo;
			//console.log('userInfo', userInfo);
			if (userInfo.nickName === undefined) {
				uni.showModal({
					title: '提示',
					content: '请先登录',
					showCancel: false,
					cancelText: '',
					confirmText: '确定',
					success: res => {
						if(res.confirm) {
							console.log('res...confirm');
							uni.switchTab({
								url: '/pages/user/index'
							});
						}
					},
					fail: () => {},
					complete: () => {
						
					}
				});
				return;
			}
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/upgrade_user_vip'
			});
		},
		complaint() {
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/complaint'
			});
		},
		help_center() {
			uni.navigateTo({
				url: '/pages/index/meme'
			});
		},
		contact_consumer() {},
		enter_huashu(title, navId) {
			uni.navigateTo({
				url:'/pages/index/huashu?title='+encodeURIComponent(title)+'&navId='+navId
			});
		}
	},
	onShareAppMessage() {
		let pages = getCurrentPages() //获取加载的页面
		let currentPage = pages[pages.length-1] //获取当前页面的对象
		let url = currentPage.route //当前页面url
		return {
			title: '恋爱话术',
			path: url,
			success: function() {
			},
			fail: function() {
			}
		};
	},
	created() {
		console.log('on created...');
	},
	mounted() {
		console.log('on mounted...');
	},
	onReady() {
		console.log('on Ready...');
	},
	onLoad() {
		console.log('onLoad...');
		uni.showShareMenu({
		    withShareTicket: true
		});
		let sysinfo = uni.getSystemInfoSync();
		let winHeight    = sysinfo.windowHeight;
		this.contentHeight   = winHeight - 60; 
		let searchViewHeight = 40;
		this.isIphoneX = getApp().globalData.isIphoneX;
		this.platForm = getApp().globalData.platform;
		this.searchViewBottom = 0;
		this.firstNavTop = 20;
		this.iconSize = searchViewHeight - 10;
		this.getNavList();
	}
}
